// 
//  BS Modal
import Modal from "bootstrap/js/dist/modal";

export default {
  methods: {
    showModal() {
      this.modal.show(); //
    },
    hideModal() {
      this.modal.hide(); //
    },
  },
  // html(htmlDOM)
  mounted() {
    //  6. BS modal  ref="modal"   this.modal
    //  https://bootstrap5.hexschool.com/docs/5.1/components/modal/#methods
    this.modal = new Modal(this.$refs.modal); // this.modal = Modal html ref  modal
    // this.modal.show(); //
  },
};
